<h1 id="python-data-storage" tabindex="-1"><a class="header-anchor" href="#python-data-storage" aria-hidden="true">#</a> Python Data Storage</h1>
<p>When the user closes our program, all our variables are lost. What if we want to remember them til next time the user runs our program? Then we need to store their values in files on the file system. Let's take a look at how to do that in this lecture.</p>
